import React, { Fragment, useEffect } from "react";

import changeTitle from "../../utils/changeTitle";
import H4 from "../../components/Headings/H4";
import BasicTable from "../../components/Tables/BasicTable";
import FormButton from "../../components/FormElements/Buttons/FormButton";
import FormGroup from "../../components/FormElements/FormGroup";

const Coupons = () => {
  const coupons = [
    { code: "Areeb50off", percentage: 50, amount: 51 },
    { code: "Areeb50off", percentage: 50, amount: 51 },
    { code: "Tony1005off", percentage: 100, amount: 51 },
  ];

  useEffect(() => {
    changeTitle("Coupon");
  }, []);

  return (
    <Fragment>
      <BasicTable
        heading="Price List"
        titles={[
          "Coupon Code",
          "Discount Percentage",
          "Discount Amount",
          "Actions",
        ]}
      >
        {coupons.map((item, index) => {
          return (
            <tr key={Math.random()}>
              <td>{index + 1}</td>
              <td>{item.code}</td>
              <td>{item.percentage}</td>
              <td>{item.amount}</td>
              <td>Delete</td>
            </tr>
          );
        })}
      </BasicTable>

      <hr />

      <H4>Create New Coupon</H4>

      <div className="basic-form">
        <form>
          <FormGroup label="Coupen Code">
            <input
              type="name"
              className="form-control"
              placeholder="Coupon Code"
            />
          </FormGroup>

          <FormGroup label="Coupen Code">
            <select className="form-control">
              <option value="percentage">Percentage</option>
              <option value="amount">Amount</option>
            </select>
          </FormGroup>

          <FormGroup label="Amount / %age">
            <input
              type="number"
              className="form-control"
              placeholder="Amount / percentage"
            />
          </FormGroup>

          <FormGroup>
            <FormButton>Add Event Item</FormButton>
          </FormGroup>
        </form>
      </div>
    </Fragment>
  );
};

export default Coupons;
