import React, { Fragment } from "react";
import H4 from "../Headings/H4";

const BasicTable = (props) => {
  const { heading, titles = [], children, style, ...rest } = props;
  console.log(style);
  return (
    <Fragment>
      <div className="table-responsive">
        {heading && <H4>{heading}</H4>}
        <table {...rest} id="" className="table table-responsive-md">
          {children ? (
            <Fragment>
              <thead>
                <tr>
                  <th style={{ width: "80px" }}>#</th>
                  {titles.map((title) => {
                    return <th key={Math.random()}>{title}</th>;
                  })}
                </tr>
              </thead>
              <tbody>{children}</tbody>
            </Fragment>
          ) : (
            <tr style={{ textAlign: "center", backgroundColor: "#ccc" }}>
              <td className="p-3">No records to show</td>
            </tr>
          )}
        </table>
      </div>
    </Fragment>
  );
};
