import React, { useEffect } from "react";

import changeTitle from "../../utils/changeTitle";
import BasicTable from "../../components/Tables/BasicTable";
import FormButton from "../../components/FormElements/Buttons/FormButton";
import FormGroup from "../../components/FormElements/FormGroup";
import H4 from "../../components/Headings/H4";

const Events = () => {
  const events = [
    {
      name: "Edilaide Family Show",
      location: "Edwards Park Anzac Hwy, Adelaide SA 5000",
    },
  ];

  useEffect(() => {
    changeTitle("Events");
  }, []);

  return (
    <div>
      <BasicTable heading="Events" titles={["Name", "Location", "Action"]}>
        {events.map((event, index) => {
          return (
            <tr key={Math.random()}>
              <td>{index + 1}</td>
              <td>{event.name}</td>
              <td>{event.location}</td>
              <td>Price List / Coupons / Sessions</td>
            </tr>
          );
        })}
        <tr></tr>
      </BasicTable>

      <hr />

      <H4>Create New Event</H4>

      <div className="basic-form">
        <form>
          <FormGroup label="Event Name *">
            <input
              type="text"
              className="form-control"
              placeholder="Event Name"
            />
          </FormGroup>
          <FormGroup label="Event Description *">
            <textarea
              name=""
              placeholder="Event Description"
              className="form-control"
              rows="4"
              id="comment"
            ></textarea>
          </FormGroup>
          <FormGroup label="Start Date/Time *">
            <input
              className="form-control input-limit-datepicker"
              type="datetime-local"
              name="daterange"
            />
          </FormGroup>
          <FormGroup label="End Date/Time *">
            <input
              className="form-control input-limit-datepicker"
              type="datetime-local"
              name="daterange"
            />
          </FormGroup>
          <FormGroup label="Event Location">
            <input
              type="email"
              className="form-control"
              placeholder="Event Location"
            />
          </FormGroup>
          <FormGroup label="Event Map">
            <input
              type="email"
              className="form-control"
              placeholder="Event Map"
            />
          </FormGroup>

          <FormGroup>
            <FormButton>Create</FormButton>
          </FormGroup>
        </form>
      </div>
    </div>
  );
};

export default Events;
